.header__link{
    font-weight: 400;
    font-size: 18px;
    line-height: 22px;
    color: white;
    text-decoration: none;
    cursor: pointer;
    border: none;
    padding: 0;
    background-color: transparent;
}

.header__link:hover{
    opacity: 0.6;
    transition: opacity 0.2s ease-out;
}